.question{
    margin: 100px 20% 0 20%;

    border: 8px solid rgb(217, 233, 248);
}

.question-text{
    margin: 0;
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: center;

    background-color: rgb(217, 233, 248);
    font-family: '游ゴシック';
    color: rgb(0, 0, 100);
    font-size: 25px;
}

.options{
    padding: 0;
}

.option{
    list-style-type: none;
    margin: 30px 70px;
    padding: 20px;
    text-align: center;

    font-size: 20px;

    background-color: rgb(185, 255, 209);
    border-radius: 15px;
    border-bottom: 7px solid rgb(0, 116, 77);

    transition: all 0.2s 0s ease;
}

.option-checked{
    list-style-type: none;
    margin: 30px 70px;
    padding: 20px;
    text-align: center;

    font-size: 20px;

    background-color: rgb(85, 255, 198);
    border-radius: 15px;
    border-bottom: 0;

    transform: translateY(5px);
    transition: all 0.2s 0s ease;
}

input[type="radio"]{
    display: none;
}

@media (min-width: 481px) and (max-width: 1024px){
    .question{
        margin: 100px 50px 0 50px;
    }

    .question-text{
        padding: 10px 20px;
        font-size: 30px;
    }

    .option{
        margin: 30px;
        padding: 10px 15px;
        font-size: 25px;
    }
    .option-checked{
        margin: 30px;
        padding: 10px 15px;
        font-size: 25px;
    }
}

@media(max-width: 480px){
    .question{
        margin: 100px 10px 0 10px;
    }

    .question-text{
        padding: 10px 20px;
        font-size: 18px;
    }

    .option{
        margin: 20px;
        padding: 10px 15px;
        font-size: 14px;
    }
    .option-checked{
        margin: 20px;
        padding: 10px 15px;
        font-size: 14px;
    }
    
}