.suggestion{
    width: 100%;
    height: 100%;
    margin: 0;
    text-align: center;
    background-color: rgba(80, 80, 80, 0.452);
    position: fixed;
    top: 1%;
}

.section{
    background-color: aliceblue;
    margin: 50px 20%;
    padding: 30px;

    animation: popupSuggestion 0.5s ease 0s 1 normal forwards;
}

.text1{
    text-align: left;
    margin-left: 5%;
}

.anime-picture{
    width: 75%;
}

.show-detail-button{
    display: block;
    text-decoration: none;
    padding: 20px;
    margin: 40px 200px; 

    font-size: 15px;
    color: rgb(0, 80, 53);
    background-color: aquamarine;
    border-radius: 15px;

    transition: all 0.4s 0s ease;
}

.show-detail-button:hover{
    background-color: rgb(235, 255, 121);

    transform: translateY(-9px);
    transition: all 0.4s 0s ease;
}

.close-button{
    border: none;
    background-color: rgba(255, 255, 255, 0);
    float: right;
    margin-top: 10px;
    margin-right: 30px;

    color: white;
    font-size: 50px;
    outline: none;

    transition: all 0.4s 0s ease;
}

.close-button:hover{
    transform: rotate(180deg);
    transition: all 0.4s 0s ease;
}






.button-wrapper{
    text-align: center;
}

.diagnosis-button{
    width: 20vw;
    height: auto;
    padding: 30px 20px;
    margin: 100px;

    background-color: rgb(217, 233, 248);
    font-size: 25px;
    color: rgb(0, 0, 100);
    border-radius: 15px;
    border: none;
    outline: none;

    transition: all 0.4s 0s ease;
}

.diagnosis-button:hover{
    background-color: rgb(0, 0, 100);
    color: white;

    transition: all 0.4s 0s ease;
}

.diagnosis-button-push{
    animation: pushDiagnosisButton 1.2s ease forwards;
}






@media (min-width: 481px) and (max-width: 1024px){
    .diagnosis-button{
        width: 300px;
        height: auto;
        padding: 40px;
        margin: 80px;
        font-size: 25px;
    }
}

@media(max-width: 480px){
    .diagnosis-button{
        width: 150px;
        height: auto;
        padding: 25px;
        margin: 50px;
        font-size: 18px;
    }
}