@keyframes pushDiagnosisButton{
    20%{
        transform: scale(1.5);
    }

    30%{
        transform: scale(0);
    }

    100%{
        transform: scale(0);
    }
}

@keyframes popupSuggestion{
    0%{
        transform: scale(0);
    }

    100%{
        transform: scale(1);
    }
}
